//amircss
.pagination {

    .page-item .page-link {
        border: 0;
        border-radius: 30px !important;
        transition: all .3s;
        padding: 0px 11px;
        margin: 0 3px;
        min-width: 30px;
        text-align: center;
        box-shadow: none;
        height: 30px;
        line-height: 30px;
        color: $black-color;
        cursor: pointer;
        font-size: $font-size-base;
        text-transform: uppercase;
        background: rgba(218, 242, 255, 0.2);

        &:hover,
        &:focus {
            color: $black-color;
            background-color: $opacity-gray-3;
            border: none;
        }
    }

    .arrow-margin-left,
    .arrow-margin-right {
        position: absolute;
    }

    .arrow-margin-right {
        right: 0;
    }

    .arrow-margin-left {
        left: 0;
    }

    .page-item.active>.page-link {
        color: $light-gray;
        box-shadow: 0px 5px 25px 0px rgba(0, 0, 0, 0.2);

        &,
        &:focus,
        &:hover {
            background-color: $default-color;
            border-color: $default-color;
            color: $white-color;
        }
    }

    .page-item.disabled>.page-link {
        opacity: .5;
        background-color: rgba(255, 255, 255, .2);
        color: $white-color;
    }

    // Colors
    &.pagination-info {
        .page-item.active>.page-link {

            &,
            &:focus,
            &:hover {
                background-color: $brand-info;
                border-color: $brand-info;
            }
        }
    }

    &.pagination-success {
        .page-item.active>.page-link {

            &,
            &:focus,
            &:hover {
                background-color: $brand-success;
                border-color: $brand-success;
            }
        }
    }

    &.pagination-primary {
        .page-item.active>.page-link {

            &,
            &:focus,
            &:hover {
                background-color: #daf2ff;
                border-color: $brand-primary;
            }
        }
    }

    &.pagination-warning {
        .page-item.active>.page-link {

            &,
            &:focus,
            &:hover {
                background-color: $brand-warning;
                border-color: $brand-warning;
            }
        }
    }

    &.pagination-danger {
        .page-item.active>.page-link {

            &,
            &:focus,
            &:hover {
                background-color: $brand-danger;
                border-color: $brand-danger;
            }
        }
    }

    &.pagination-neutral {
        .page-item>.page-link {
            color: $white-color;

            &:focus,
            &:hover {
                background-color: $opacity-2;
                color: $white-color;
            }
        }

        .page-item.active>.page-link {

            &,
            &:focus,
            &:hover {
                background-color: $white-bg;
                border-color: $white-bg;
                color: $brand-primary;
            }
        }
    }

}

.pagination-container {
    display: flex;
    align-items: center;
}